@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../assets/fonts/Raleway-Regular.woff") format("woff"),
  url("../assets/fonts/Raleway-Regular.ttf") format("truetype");
}

@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../assets/fonts/Raleway-Bold.woff") format("woff"),
  url("../assets/fonts/Raleway-Bold.ttf") format("truetype");
}